.header {
  display: grid;
  grid-area: header;
  margin: 60px 0 20px 0; }
  .header__name {
    font-size: 5.3em;
    font-weight: 600;
    grid-row: 1/2;
    grid-column: 1/2;
    justify-self: start; }
  .header__scores {
    grid-row: 1/2;
    grid-column: 2/3;
    justify-self: end; }
    .header__scores > * + * {
      margin-left: 5px; }
  .header__description {
    grid-row: 2/3;
    grid-column: 1/2;
    justify-self: start;
    -ms-flex-item-align: center;
        align-self: center; }
  .header__new-game {
    grid-row: 2/3;
    grid-column: 2/3;
    justify-self: end; }



